.site__inner {
  padding: 16px;

  .site__title {
    position: sticky;
    top: 10px;
    left: 0;
    width: 70%;
    height: 5vw;
    font-size: 4vw;
    font-weight: 900;
    line-height: 1.6;
    font-family: var(--mainKor-font);
    text-transform: uppercase;
    border-bottom: 0.4vw solid var(--black100);
    margin-bottom: 100px;

    @media (max-width: 800px) {
      width: 100%;
      margin-bottom: 10vw;
      font-size: 30px;
      height: auto;
      top: 68px;
      z-index: 1000;
    }

    em {
      font-size: 1.25rem;
      font-weight: 400;
      line-height: 2;
    }
  }

  .site__wrap {
    .site__item {
      height: 70vh;
      @include flex-center;
      flex-direction: column;
      position: sticky;
      left: 0;
      border-radius: 1rem;
      background-color: var(--mainBg-color);
      border: 4px solid var(--point-color1);

      &.s1 {
        top: 180px;

        @media (max-width: 800px) {
          top: 140px;
        }
      }
      &.s2 {
        top: 200px;

        @media (max-width: 800px) {
          top: 160px;
        }
      }
      &.s3 {
        top: 220px;

        @media (max-width: 800px) {
          top: 180px;
        }
      }
      &.s4 {
        top: 240px;

        @media (max-width: 800px) {
          top: 200px;
        }
      }
      &.s5 {
        top: 260px;

        @media (max-width: 800px) {
          top: 220px;
        }
      }
      .num {
        font-size: 5vw;
        line-height: 1;
        font-weight: 900;
        font-family: var(--mainNum-font);
        position: absolute;
        left: 1vw;
        top: 1vw;

        @media (max-width: 800px) {
          font-size: 80px;
          left: 20px;
          top: 20px;
        }
      }
      .title {
        text-align: center;
        margin-bottom: 1rem;

        > h3 {
          font-size: 2.6vw;
          line-height: 1;
          font-weight: 900;
          text-transform: uppercase;
          text-decoration: underline;

          @media (max-width: 800px) {
            font-size: 26px;
          }
        }
      }
      .text {
        font-family: var(--mainKor-font);
        margin-bottom: 1rem;
        width: 70%;
        text-align: center;
      }
      .text_skill {
        font-size: 0.9rem;
        margin-top: 10px;
      }
      .btn {
        margin-top: 10px;
        a {
          background-color: var(--baseBg);
          border: 2px solid transparent;
          color: var(--black200);
          font-size: 1rem;
          transition: all 0.3s;
          margin: 0 2px;
          padding: 3px 5px;
          border-radius: 6px;
          box-sizing: border-box;

          &:hover {
            background-color: transparent;
            border: 2px solid var(--baseBg);
            color: var(--baseBg);
            box-sizing: border-box;
          }
        }
      }
      .info {
        position: absolute;
        left: 0;
        bottom: 3%;
        width: 98%;
        margin-left: 1%;
        border-top: 1px solid var(--black);
        border-bottom: 1px solid var(--black);
        display: flex;
        justify-content: space-between;

        @media (max-width: 800px) {
          flex-direction: column;
          width: 90%;
          margin-left: 5%;
          padding: 5px 0;
        }

        span {
          font-family: var(--mainKor-font);
          font-size: 12px;
          padding: 3px 0;
          text-transform: uppercase;
        }
      }
    }
  }
}
